<template>
  <v-autocomplete
    v-model="times"
    :loading="isLoading"
    :search-input.sync="search"
    :items="items"
    item-text="nome"
    return-object
    hide-no-data
    @update:error="updateError"
    @input="valueSelected"
    cache-items
    multiple
    :menu-props="getMenuProps()"
    placeholder="Selecione dois times"
    :rules="rules"
  >
    <template v-slot:selection="data">
      <v-chip
        v-bind="data.attrs"
        :input-value="data.selected"
        close
        @click="data.select"
        @click:close="remover(data.item)"
      >
        <v-avatar left>
          <v-img :src="data.item.url_escudo_png"></v-img>
        </v-avatar>
        {{ data.item.nome }}
      </v-chip>
    </template>
    <template v-slot:item="{ item }" v-if="items && items.length > 0">
      <v-list-item-avatar v-if="item && item.url_escudo_png">
        <v-img :src="item.url_escudo_png" />
      </v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title v-text="item.nome"></v-list-item-title>
        <v-list-item-subtitle v-text="item.nome_cartola"></v-list-item-subtitle>
      </v-list-item-content>
    </template>
  </v-autocomplete>
</template>

<script>
import _ from "lodash";
import axios from "axios";
import api from "../../services/api";

export default {
  data() {
    return {
      input: "",
      search: "",
      items: [],
      isLoading: false,
      rules: [
        (items) => {
          if (items && items.length < 2) {
            return "Selecione dois times para a dupla";
          } else if (items && items.length > 2) {
            return "Somente dois times devem ser selecionados";
          }
          return false;
        },
      ],
    };
  },
  computed: {
    times: {
      get: function() {
        return this.value;
      },
      set: function(value) {
        this.$emit("emittedtimes", value);
      }
    }
  },
  props: {
    value: null
  },
  methods: {
    doSearch(value) {
      this.isLoading = true;
      api.get("buscarTime", { params: { nome: value } }).then((res) => {
        this.items = [...res.data];
        this.isLoading = false;
      });
    },
    remover(item) {
      const index = this.times.indexOf(item);
      if (index > -1) {
        this.times.splice(index, 1);
      }
    },
    valueSelected(value) {
      this.search = "";
      this.$emit("input", value);
    },
    updateError(error) {
      this.$emit("updateError", error);
    },
    getMenuProps() {
      if(this.$vuetify.breakpoint.xs){
        return {maxHeight: "45vh"};
      }
      return  {};
    }
  },
  watch: {
    search: function (newValue) {
      this.debounceSearch(newValue);
    },
  },
  created() {
    this.debounceSearch = _.debounce(this.doSearch, 400, {});
  },
};
</script>

