<template>
  <v-app id="inspire">
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" no-gutters justify="center">
          <v-col cols="12" sm="12" md="4">
            <v-card class="elevation-12">
              <v-toolbar color="primary" dark flat>
                <v-toolbar-title>Realize seu login</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <div style="margin-left: 30px;">
                    <v-alert v-model="displayErro" color="red" dense outlined>
                      <v-row align="center">
                        <v-col class="grow">{{erroMessage}}</v-col>
                        <v-col class="shrink" @click="displayErro = false">
                          <v-icon>mdi-close</v-icon>
                        </v-col>
                      </v-row>
                    </v-alert>
                  </div>
                  <v-text-field
                    label="E-mail"
                    name="login"
                    v-model="usuario.login"
                    prepend-icon="mdi-account-circle"
                    type="text"
                  />

                  <input-password-vue v-model="usuario.senha" prepend-icon="mdi-lock" />
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn color="success" class="text-decoration-none" to="/sign-up">Cadastre-se</v-btn>
                <v-spacer />
                <v-btn color="primary" @click="realizarLogin()">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import InputPasswordVue from "../components/input/InputPassword.vue";
import api from "../services/api";
import { setUser } from '../utils';

export default {
  props: {
    source: String,
  },
  data() {
    return {
      usuario: {
        senha: "",
        login: "",
      },
      erroMessage: null,
      displayErro: false,
    };
  },
  methods: {
    async realizarLogin() {
      try {
       const data =  await api.post("/usuario/login", {
          password: this.usuario.senha,
          email: this.usuario.login,
        });
        setUser(data.headers.authorization);
        this.$router.push("/");
      } catch (error) {
        console.log(error)
        this.erroMessage = error.response.data.erro;
        this.displayErro = true;
      }
    },
  },
  components: {
    InputPasswordVue,
  },
};
</script>