<template>
  <v-container fluid>
    <v-row justify="center">
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Nova liga</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="8" md="6">
                  <v-text-field label="Ttulo" required v-model="liga.titulo" />
                </v-col>  
                <v-col cols="12" sm="8" md="6">
                  <v-select v-model="liga.tipo" :items="tiposLiga" />
                </v-col>
                <v-col cols="12" sm="4">
                  <v-text-field
                    label="Nmero de times"
                    type="number"
                    required
                    v-model="liga.quantidadeTimes"
                  />
                </v-col>
                <v-col cols="12" sm="4">
                  <v-text-field
                    label="Quantidade de rodadas"
                    type="number"
                    required
                    v-model="liga.quantidadeRodadas"
                  />
                </v-col>
                <v-col cols="12" sm="4">
                  <v-text-field prefix="R$" label="Valor" required v-model="liga.valor" />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">Fechar</v-btn>
            <v-btn color="blue darken-1" text @click="salvar">Salvar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import MenuDatePicker from "../../components/datepicker/MenuDatePicker.vue";
import LigaService from "../../domain/liga/LigaService.js";
import Liga from "../../domain/liga/Liga.js";
import api from "../../services/api";

export default {
  data() {
    return {
      liga: new Liga(),
      overlay: true,
      tipoLiga: "Eliminao de duplas",
      tiposLiga: ["Mata a mata", "Eliminao de duplas"],
    };
  },
  props: {
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    close() {
      this.$emit("close");
    },
    async salvar() {
      this.overlay = false;
      const res = await api.post("/liga", this.liga).then(
        (res) => {
          this.$emit("novaLiga", res.data);
          this.close();
        },
        (err) => console.log(err.response.data)
      );
    },
  },
  components: {
    MenuDatePicker,
  },
};
</script>

