<template>
  <v-app>
    <v-content>
      <v-container fluid>
        <v-alert :type="type" v-show="showAlert">{{mensagem}}{{willRedirect? redirectTime : null}}</v-alert>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="12" md="6">
            <v-card elevation="10">
              <v-card-title>Cadastre-se</v-card-title>
              <v-card-text>
                <v-text-field v-model="usuario.nome" label="Nome" required></v-text-field>
                <v-text-field v-model="usuario.email" label="E-mail" required></v-text-field>
                <input-password-vue v-model="usuario.senha" />
                <v-card-actions>
                  <v-btn @click="clear" class="mr-4 error">Cancelar</v-btn>
                  <v-spacer />
                  <v-btn class="mr-4 success" @click="submit">Cadastre-se!</v-btn>
                </v-card-actions>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import InputPasswordVue from "../components/input/InputPassword.vue";
import UsuarioService from "../domain/usuario/UsuarioService.js";
import Usuario from "../domain/usuario/Usuario.js";
import api from "../services/api";

export default {
  data: () => ({
    usuario: new Usuario(),
    mensagem: "",
    type: null,
    showAlert: false,
    redirectTime: 4,
    willRedirect: false,
  }),
  methods: {
    submit() {
      api.post("/usuario", this.usuario).then(() => {
        this.mensagem =
          "Usurio salvo com sucesso! Redirecionando para o login em: ";
        this.countDown();
        this.showAlert = true;
        this.type = "success";
      });
    },
    clear() {
      this.usuario = new Usuario();
    },
    countDown() {
      this.willRedirect = true;
      if (this.redirectTime > 0) {
        setTimeout(() => {
          this.redirectTime -= 1;
          this.countDown();
        }, 1000);
      } else {
        this.$router.push("/login");
      }
    },
  },
  components: {
    InputPasswordVue,
  },
  created() {
    this.service = new UsuarioService(this.$resource);
  },
};
</script>