<script>
import { rotasHome } from "../router/rotasHome.js";

export default {
  props: {
    source: String
  },

  data: () => ({
    drawer: null,
    titulo: "Cartoloukos",
    corEstilo: "black",
    user: {
      name: "",
      mail: ""
    },
    rotasHome
  }),
  methods: {
    filtraPorMenuAtivo(array) {
      return array.filter(function(value) {
        return value.menu != false;
      });
    }
  }
};
</script>

