<template>
  <v-app>
    <v-content>
      <v-btn
        absolute
        right
        class="mx-2 my-2"
        fab
        :small="this.$vuetify.breakpoint.name!='xs'"
        dark
        color="primary"
        @click="abrirModal"
      >
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      <lista :ligas="ligas" />
      <modal :dialog="dialog" @close="fecharModal" @novaLiga="adicionarNovaLiga"/>
    </v-content>
  </v-app>
</template>

<script>
import moment from "moment";
import ModalNovaLiga from "./ModalNovaLiga.vue";
import Lista from "./Lista.vue";
import api from "../../services/api";

export default {
  data() {
    return {
      dialog: false,
      ligas: [],
    };
  },
  methods: {
    abrirModal() {
      this.dialog = true;
    },
    fecharModal() {
      this.dialog = false;
    },
    async buscarLigas() {
      try {
        const res = await api.get("liga");
        this.ligas = res.data;
      } catch (error) {
        console.log(error);
      }
    },
    adicionarNovaLiga(liga) {
      this.ligas.push(liga);
    }
  },
  components: {
    modal: ModalNovaLiga,
    lista: Lista,
  },
  created() {
    this.buscarLigas();
  },
};
</script>

