<template>
  <v-dialog v-model="dialog" max-width="290">
    <v-card style="margin-left: auto; margin-right: auto; padding-buttom: 0;">
      <v-card-title class="headline">{{title}}</v-card-title>
      <v-card-text>{{text}}</v-card-text>
      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text @click="() => this.$emit('close')">{{cancelButton}}</v-btn>
        <v-btn color="error" text @click="excluir">{{confirmButton}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  computed: {
    dialog: {
      get: function () {
        return this.value;
      },
      set: function (val) {
        this.$emit("emitteddialog", val);
      },
    },
  },
  props: {
    value: { required: true },
    funExcluir: { type: Function, required: true },
    idExcluir: { required: true },
    text: { default: "Deseja realmente excluir?" },
    title: { default: "Excluir" },
    confirmButton: { default: "Excluir" },
    cancelButton: { default: "Fechar" },
  },
  methods: {
    async excluir() {
      try {
        await this.funExcluir(this.idExcluir);
        this.dialog = false;
        this.$emit("close");
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>