<template>
  <v-content>
    <v-container>
      <v-tabs v-model="tab" @change="buscarDuplas">
        <v-tab key="pontuacao">Pontuaes</v-tab>
        <v-tab key="duplas">Duplas</v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item key="pontuacao">
          <v-data-table
            :mobile-breakpoint="0"
            :loading="loadingPontuacao"
            loading-text="Carregando pontuaes"
            :items="getSortedPontuacao"
            :headers="headers"
            item-key="nome"
            hide-default-footer
          >
            <template v-slot:body="{ items }">
              <tbody>
                <tr
                  v-for="(item, index) in items"
                  :key="item.nome"
                  :style="'background-color:' + getRowColor(index)"
                >
                  <td>{{ item.nome }}</td>
                  <td>{{item.pontuacaoRodada}}</td>
                  <td>{{item.ultimaPontuacao}}</td>
                </tr>
              </tbody>
            </template>
          </v-data-table>
        </v-tab-item>
        <v-tab-item key="duplas">
           <v-btn
            right
            absolute
            top
            class="mx-2 my-2"
            fab
            :small="this.$vuetify.breakpoint.name!='xs'"
            dark
            color="primary"
            @click="abrirModalAdicionarDupla = true"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
          <v-row>
            <v-col>
              <v-data-table  
                style="margin-top: 30px" 
                loading-text="Carregando duplas" 
                :mobile-breakpoint="0" 
                :loading="loadingDuplas" 
                :items="duplas" 
                :headers="headersDuplas" 
                hide-default-footer 
                no-data-text="Nenhuma dupla adicionada"
              >
                <template v-slot:[`item.opcoes`]="{ item }">
                  <v-btn 
                    icon 
                    @click="()=> {
                      idDuplaRemover = item.idDupla;
                      abrirModalRemoverDupla = true
                    }"
                  >
                    <v-icon color="error">mdi-delete</v-icon>
                  </v-btn>
                </template>
              </v-data-table>
            </v-col>
          </v-row>
        </v-tab-item>
      </v-tabs-items>
      <modal-adicionar-dupla
        :dialog="abrirModalAdicionarDupla"
        @close="abrirModalAdicionarDupla = false"
        @novaDupla="adicionarNovaDupla"
      />
    </v-container>
    <modal-confirmacao-exclusao-vue v-model="abrirModalRemoverDupla" :funExcluir="removerDupla" :idExcluir="idDuplaRemover" @close="abrirModalRemoverDupla = false"/>
  </v-content>
</template>

<script>
import ModalAdicionarDupla from "./ModalAdicionarDupla";
import api from '../../services/api';
import ModalConfirmacaoExclusaoVue from '../../components/modal/ModalConfirmacaoExclusao.vue';

export default {
  data() {
    return {
      tab: null,
      times: [
        { nome: "Luciano e Z", pontuacaoRodada: 11, ultimaPontuacao: 14 },
        { nome: "Cartoloukos e Joo", pontuacaoRodada: 10, ultimaPontuacao: 7 },
        {
          nome: "Pessoa x e Pessoa y",
          pontuacaoRodada: 10,
          ultimaPontuacao: 8,
        },
        {
          nome: "Mestre dos magos e Pikachu",
          pontuacaoRodada: 11,
          ultimaPontuacao: 12,
        },
        { nome: "Mr. Been e Chaves", pontuacaoRodada: 11, ultimaPontuacao: 13 },
      ],
      duplas: [],
      headers: [
        { text: "Dupla", value: "nome" },
        { text: "Pontuao da ltima rodada", value: "ultimaPontuacao" },
        { text: "Pontuao da rodada atual", value: "pontuacaoRodada" },
      ],
      headersDuplas: [
        { text: "Jogador 1", value: "jogador1" },
        { text: "Jogador 2", value: "jogador2" },
        { text: "Opes", value: "opcoes" },
      ],
      abrirModalAdicionarDupla: false,
      loadingDuplas: true,
      loadingPontuacao: false,
      abrirModalRemoverDupla: false,
      idDuplaRemover: null
    };
  },
  methods: {
    adicionarNovaDupla(dupla) {
      this.duplas.push(dupla);
    },
    async removerDupla(codigo) {
     try {
       await api.delete("duplas", {params: {codigo}});
       const index = this.duplas.findIndex(dupla => dupla.idDupla === codigo);
       this.duplas.splice(index);
     } catch(error) {
       console.log(error.response);
     }
    },
    sortPontuacao(array) {
      return array.sort((a, b) => {
        if (a.pontuacaoRodada < b.pontuacaoRodada) {
          return 1;
        } else if (a.pontuacaoRodada > b.pontuacaoRodada) {
          return -1;
        } else if (a.pontuacaoRodada === b.pontuacaoRodada) {
          return b.ultimaPontuacao - a.ultimaPontuacao;
        }
        return 0;
      });
    },
    getRowColor(index) {
      if (index < 1) {
        return "#90EE90";
      } else if (index > 1) {
        return "#FF6347";
      }
    },
    async buscarDuplas(tabSelecionada) {
      if (tabSelecionada == 1) {
        try {
          const res = await api.get("duplas", {
            params: { idLiga: this.$route.params.id },
          });
          this.duplas = res.data.map((time) => {
            return { jogador1: time.nomeJogador1, jogador2: time.nomeJogador2, idDupla: time.id };
          });
        } catch (error) {
          console.log(error.response);
        }
        this.loadingDuplas = false;
      }
    },
  },
  computed: {
    getSortedPontuacao() {
      return this.sortPontuacao(this.times);
    },
  },
  components: {
    "modal-adicionar-dupla": ModalAdicionarDupla,
    ModalConfirmacaoExclusaoVue
  },
};
</script>

