<template>
  <v-text-field
    label="Senha"
    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
    :prepend-icon="prependIcon"
    :type="show ? 'text' : 'password'"
    v-bind:value="value"
    @input="$emit('input', $event)"
    @click:append="show = !show"
  ></v-text-field>
</template>

<script>
export default {
  data() {
    return {
      show: false
    };
  },
  props: {
    prependIcon: "",
     value: ""
  }
};
</script>
