export default class {
    constructor(resource) {
        this._resource = resource;
    }

    salvar(entidade) {
        return this._resource.save(entidade).then(res => {
            return this.tratarRetorno(res)
        }, err => {
            return this.tratarErro(err);
        });
    }

    listar() {
        return this._resource.query().then(res => {
            return this.tratarRetorno(res)
        }, err => {
            return this.tratarErro(err);
        });
    }

    alterar(entidade) {
        return this._resource.update(entidade).then(res => {
            return this.tratarRetorno(res);
        }, err => {
            return this.tratarErro(err);
        });
    }

    visualizar(id) {
        return this._resource.query({ id }).then(res => {
            return this.tratarRetorno(res);
        }, err => {
            return this.tratarErro(err);
        });
    }

    deletar(id) {
        return this._resource.delete({ id }).then(res => {
            return this.tratarRetorno(res);
        }, err => {
            return this.tratarErro(err);
        });;
    }

    tratarRetorno(res) {
        return res.json();
    }

    tratarErro(res) {
        const err = {};
        if (res.errors) {
            err.erros = res.erros;
        } else {
            err.erros = "Algo de inesperado aconteceu e no foi possvel se conectar ao servidor."
        }

        return Promise.reject(err);
    }
}