export default class {
    constructor(titulo, dataInicial, dataFinal, valor, quantidadeTimes, quantidadeRodadas, tipo = "Eliminao de duplas") {
        this.titulo = titulo;
        this.dataInicial = dataInicial;
        this.dataFinal = dataFinal;
        this.valor = valor;
        this.quantidadeTimes = quantidadeTimes;
        this.quantidadeRodadas = quantidadeRodadas;
        this.tipo = tipo;
    }
}