<template>
  <v-container fluid>
    <v-row justify="center">
      <v-dialog v-model="dialog" persistent max-width="600px" :fullscreen="$vuetify.breakpoint.xs">
        <v-card>
          <v-card-title>
            <span class="headline">Nova dupla</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-alert v-model="displayErro" color="red" dense outlined>
                <v-row align="center">
                  <v-col class="grow">{{erroMessage}}</v-col>
                  <v-col class="shrink" @click="displayErro = false">
                    <v-icon>mdi-close</v-icon>
                  </v-col>
                </v-row>
              </v-alert>
              <v-row>
                <v-col cols="12">
                  <autocomplete-time v-model="times" @updateError="error => this.hasError = error" />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions :style="$vuetify.breakpoint.xs? {marginTop: '50vh'}: {}">
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">Fechar</v-btn>
            <v-btn color="blue darken-1" text @click="salvar" :disabled="hasError">Salvar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
import AutocompleteTime from "../../components/input/AutocompleteTime.vue";
import api from "../../services/api";
export default {
  data() {
    return {
      jogador1: "",
      jogador2: "",
      hasError: true,
      times: [],
      erroMessage: null,
      displayErro: false,
      dupla: null,
    };
  },
  components: {
    AutocompleteTime,
  },
  props: {
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    close() {
      this.times = null;
      this.$emit("close");
    },
    async salvar() {
      const dupla = {
        nomeJogador1: this.times[0].nome,
        nomeJogador2: this.times[1].nome,
        idCartolaJogador1: this.times[0].time_id,
        idCartolaJogador2: this.times[1].time_id,
        idLiga: this.$route.params.id,
      };
      try {
        const res = await api.post("/dupla", dupla);
        this.$emit("novaDupla", {
          jogador1: dupla.nomeJogador1,
          jogador2: dupla.nomeJogador2,
          idDupla: res.data.id,
        });

        this.times = null;
        this.close();
      } catch (error) {
        console.log(error);
        this.erroMessage = error.response.data.erro;
        this.displayErro = true;
      }
    },
  },
};
</script>
